const ip = {
	state: ()=>({
		ip:''
	}),
	mutations: {
		LOAD_IP(state, ip){
			state.ip = ip;
		},
	},
	actions: {
		async fetchIp({commit}){
			const url = "https://api.ipify.org/?format=json"
			let serverResp=true;
			try{
				const ip = await (await fetch(`${url}`)).json()
				localStorage.setItem('ip', JSON.stringify(ip.ip))
				commit('LOAD_IP', ip.ip);
			}catch(error){
				console.error(error);
				const localIp = JSON.parse(localStorage.getItem('ip'));
				if(localIp) commit('LOAD_IP', localIp)
				else serverResp=false;
			}
			commit('SET_SERVER_RESPOND',{ip:serverResp})
		}, 
	},
	getters: {
		ip: state=>state.ip
	}
}
export default ip