<template lang="pug">
b-container.home(fluid, v-if="dataFullLoaded") 
	b-row
		b-col(cols="6", md="2")
			router-link(:to="{ name: 'Location' }")
				LocationPreview
		b-col(cols="6", md="3")
			router-link(:to="{ name: 'Weather' }")
				WeatherPreview
		b-col.mt-4.my-md-0(cols="8", sm="8", md="5")
			router-link(:to="{ name: 'Covid' }")
				CovidPreview
		b-col.mt-4.my-md-0(cols="4", sm="4", md="2")
			router-link(:to="{ name: 'News' }")
				NewsPreview
	b-row
		b-col.mt-4(cols="5", sm="6")
			CalendarPreviewDesktop.d-none.d-md-block
			router-link(:to="{ name: 'Calendar' }")
				CalendarPreviewMobile.d-block.d-md-none
		b-col.mt-4(cols="7", sm="6", @click="moveToTasks()")
			TasksPreviewDesktop.d-none.d-md-block
			TasksPreviewMobile.d-block.d-md-none
	b-row
		b-col.mt-4(cols="12") 
			.item.neuro.add-note
				router-link(:to="{ name: 'NewTask' }") 
				h2 add task
				b-icon(icon="plus-circle", font-scale="2")
</template>

<script>
import { mapGetters } from "vuex";

import LocationPreview from "../components/homePreviews/LocationPreview";
import WeatherPreview from "../components/homePreviews/WeatherPreview";
import CovidPreview from "../components/homePreviews/CovidPreview";

import CalendarPreviewDesktop from "../components/homePreviews/CalendarPreviewDesktop";
import CalendarPreviewMobile from "../components/homePreviews/CalendarPreviewMobile";

import TasksPreviewDesktop from "../components/homePreviews/TasksPreviewDesktop";
import TasksPreviewMobile from "../components/homePreviews/TasksPreviewMobile";

import NewsPreview from "../components/homePreviews/NewsPreviews";

export default {
	data: () => ({}),
	components: {
		LocationPreview,
		WeatherPreview,
		CovidPreview,
		CalendarPreviewDesktop,
		CalendarPreviewMobile,
		TasksPreviewDesktop,
		TasksPreviewMobile,
		NewsPreview
	},
	computed: {
		...mapGetters([
			"ip",
			"loc",
			"weather",
			"covid",
			"dataFullLoaded",
			"tasks"
		]),
		pathToFlag(code) {
			return `https://www.countryflags.io/${code}/flat/64.png`;
		}
	},
	methods: {
		moveToTasks() {
			this.$router.push({ name: "Tasks" });
		}
	}
};
</script>

<style lang="sass">
.home
	width: 100%
	height: 100%
	display: flex
	justify-content: center
	flex-direction: column
	max-width: 85rem
	+MW767
		max-width: 45rem
	.item
		height: 100%
		padding: 1rem
		color: var(--text1)
		&__title
			font-size: 1rem
			text-transform: uppercase
			font-weight: 800
			color: var(--text2)
			height: 10%
		h3
			font-weight: 700
		&__content
			display: flex
			height: 90%
			padding: .5rem
.add-note
	display: flex
	justify-content: center
	align-items: center
	position: relative
	h2
		align-items: center
		margin-right: .5rem
		font-weight: 800
		text-transform: uppercase
	a
		width: 100%
		height: 100%
		position: absolute
		top: 0%
		left: 0%
</style>