<template lang="pug">
.item.tasks.neuro
	.mobile-tasks
		.item__title
			h2 tasks
		.item__content.mobile-tasks__content
			.mobile-tasks__tasks 
				span You have
				h3 {{ dateFilteredTasks.length }}
				span(v-if="dateFilteredTasks.length == 1") task
				span(v-else) tasks
			.divide-h-line
			.mobile-tasks__holidays
				.mobile-tasks__holidays-container.thin-scroll-bar(
					v-if="dateFilteredHolidays.length > 0"
				)
					ul(v-for="holiday in dateFilteredHolidays", :key="holiday.id")
						li {{ holiday.name }}
				.mobile-tasks__holidays-container(v-else) 
					ul 
						li No holidays
</template>

<script>
import { mapGetters } from "vuex";
import commonTasksMethods from "../../mixins/commonTasksMethods";
export default {
	mixins: [commonTasksMethods],

	data: () => ({}),

	computed: {
		...mapGetters(["tasks", "holidays", "calendarCtx"])
	},
};
</script>

<style lang="sass">
.mobile-tasks
	&__content
		display: flex
		flex-direction: column
		align-items: flex-end
	&__tasks
		h3
			display: inline-block
			margin: 0rem .3rem
			font-weight: 800
			font-size: 1.5rem
</style>