const holidays = {
	state: ()=>({
		holidays:{}
	}),
	mutations: {
		LOAD_HOLIDAYS(state, response){
			state.holidays=response;
		},
	},
	actions: {
		async fetchHolidays({commit, rootState}){
			let serverResp=true;
			if(!localStorage.getItem('holidays')) {
				const url = "https://getfestivo.com/v2/holidays"
				const key = "025112ab560ff08c68c04e03e5469a1e"
				try{
					const countryCode = rootState.loc.loc.country_code 
					const address =`${url}?api_key=${key}&country=${countryCode}&year=2020`
					const response = await fetch(`${address}`)
					const result = await response.json()
					const localHolidays = JSON.stringify(result.holidays)
					localStorage.setItem('holidays', `[${localHolidays}]`)
					commit("LOAD_HOLIDAYS", result.holidays)
				}catch(error){
					console.error(error);
					serverResp=false;
				}
			} else {
				commit("LOAD_HOLIDAYS", JSON.parse(localStorage.getItem('holidays'))[0])
			}
			commit('SET_SERVER_RESPOND',{holidays:serverResp})
		},
	},
	getters: {
		holidays: state=>state.holidays,
	},
}
export default holidays