function saveToLocalStorage(tasks){
	localStorage.setItem("tasks", JSON.stringify(tasks));
}

const tasks = {
	state: () => ({
		tasks:[],
	}),
	mutations: {
		LOAD_TASKS_FROM_LOCAL(state){
			let localTasks = localStorage.getItem("tasks")
			if (!localTasks) {
				const example = JSON.stringify({
					id:'#1a2b',
					title: "Example",
					description: "",
					date: "1971-01-01",
					time: "00:00:00",
					status: "outdated",
				})
				localStorage.setItem("tasks", `[${example}]`)
				localTasks=localStorage.getItem("tasks")
			}
			state.tasks = JSON.parse(localTasks);
		}, 
		ADD_TASK(state, task){
			state.tasks.push(task)
			saveToLocalStorage(state.tasks)
		},
		STATUS_CHANGER(state, taskId){
			state.tasks.forEach(task => {
				if(task.id===taskId){
					if(task.status!="done") task.status="done"
					else {
						const checkDate = new Date(`${task.date}T${task.time}`).toISOString()
						const now = new Date().toISOString()
						if (checkDate < now) task.status="outdated"
						else task.status="active"
					}
				}
			});
			saveToLocalStorage(state.tasks)
		},
		DELETE_TASK(state, taskId){
			state.tasks.forEach((task, i) => {
				if(task.id===taskId) state.tasks.splice(i, 1)
			});
			saveToLocalStorage(state.tasks)
		},
		STATUS_UPDATE(state){
			const result = state.tasks.map(task=>{
				const checkDate = new Date(`${task.date}T${task.time}`).toISOString()
				const now = new Date().toISOString()
				if(task.status!="done"){
					if (checkDate < now) task.status="outdated"
					if (checkDate > now) task.status="active"
				} 
				return task
			})
			state.tasks=result
			saveToLocalStorage(state.tasks)
		}
	},
	getters: {
		tasks: state => state.tasks,
	}
}
export default tasks