<template lang="pug">
.top-bar
	.arrowBack.neuro(
		v-if="showBackArrow",
		@click="$router.go(-1)",
		:class="{ opacity0: this.$store.state.isActiveMenu }"
	)
		b-icon(icon="arrow-left")
	.switch-wrap.neuro(v-else, :class="{ neuroPressed: themeDark }")
		.power-switch
			input(type="checkbox", v-model="themeDark")
			.button
				svg.power-off
					use.line(xlink:href="#line")
					use.circle(xlink:href="#circle")
				svg.power-on
					use.line(xlink:href="#line")
					use.circle(xlink:href="#circle")
			// SVG
			svg(xmlns="http://www.w3.org/2000/svg", style="display: none")
				symbol#line(xmlns="http://www.w3.org/2000/svg", viewbox="0 0 150 150")
					line(x1="75", y1="34", x2="75", y2="58")
				symbol#circle(xmlns="http://www.w3.org/2000/svg", viewbox="0 0 150 150")
					circle(cx="75", cy="80", r="35")
	.burger.neuro(
		@click="MENU_CHANGER",
		:class="{ opacity0: this.$route.name == 'home', neuroPressed: this.$store.state.isActiveMenu, activeMenu: this.$store.state.isActiveMenu }"
	) 
		.burger__container
			.line1.lines
			.line2.lines
			.line3.lines
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
export default {
	name: "TopBar",
	data: () => ({
		showBackArrow: null,
		themeDark: true
	}),
	methods: {
		...mapMutations(["MENU_CHANGER"]),
		leftBtnContent(route) {
			if (route.name != "home") this.showBackArrow = true;
			else this.showBackArrow = false;
		}
	},
	getters: {
		...mapGetters([""])
	},
	watch: {
		themeDark() {
			if (!this.themeDark)
				document.documentElement.setAttribute("data-theme", "light");
			else document.documentElement.removeAttribute("data-theme");
		},
		$route(e) {
			this.leftBtnContent(e);
		}
	},
	mounted() {
		this.leftBtnContent(this.$route);
		this.showMenu = this.$store.state.isActiveMenu;
	}
};
</script>

<style>
/*switcher*/
.top-bar {
	width: 100%;
	height: var(--topBarHeight);
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	background-color: var(--bg2);
	top: 0%;
	left: 0%;
	padding: 0rem 2rem;
	z-index: 2;
	--btnSize: 4.5rem;
}
@media (max-width: 350px) and (orientation: portrait) {
	.top-bar {
		--btnSize: 3.5rem;
	}
}
.arrowBack {
	width: var(--btnSize);
	height: var(--btnSize);
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: var(--radius1);
}
.arrowBack .b-icon {
	font-size: 2rem;
}
.arrowBack:hover {
	cursor: pointer;
}
.switch-wrap {
	width: var(--btnSize);
	height: var(--btnSize);
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	border-radius: var(--radius1);
}
.power-switch {
	--color-invert: var(--text2);
	--width: 150px;
	--height: 150px;
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	width: var(--width);
	height: var(--height);
	transform: scale(0.25);
}
.power-switch .button {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
}

.power-switch .button .power-on,
.power-switch .button .power-off {
	height: 100%;
	width: 100%;
	position: absolute;
	z-index: 1;
	fill: none;
	stroke: var(--color-invert);
	stroke-width: 8px;
	stroke-linecap: round;
	stroke-linejoin: round;
}
.power-switch .button .power-on .line,
.power-switch .button .power-off .line {
	opacity: 0.2;
}
.power-switch .button .power-on .circle,
.power-switch .button .power-off .circle {
	opacity: 0.2;
	transform: rotate(-58deg);
	transform-origin: center 80px;
	stroke-dasharray: 220;
	stroke-dashoffset: 40;
}

.power-switch .button .power-on .line {
	opacity: 0;
	transition: opacity 0.3s ease 1s;
}
.power-switch .button .power-on .circle {
	opacity: 1;
	stroke-dashoffset: 220;
	transition: transform 0s ease, stroke-dashoffset 1s ease 0s;
}
.power-switch input {
	position: absolute;
	height: 100%;
	width: 100%;
	z-index: 2;
	cursor: pointer;
	opacity: 0;
}
.power-switch input:checked + .button:after {
	opacity: 0.15;
	transform: scale(2) perspective(1px) translateZ(0);
	backface-visibility: hidden;
	transition: opacity 0.5s ease, transform 0.5s ease;
}
.power-switch input:checked + .button .power-on,
.power-switch input:checked + .button .power-off {
	animation: click-animation 0.3s ease forwards;
	transform: scale(1);
}
.power-switch input:checked + .button .power-on .line,
.power-switch input:checked + .button .power-off .line {
	animation: line-animation 0.8s ease-in forwards;
}
.power-switch input:checked + .button .power-on .circle,
.power-switch input:checked + .button .power-off .circle {
	transform: rotate(302deg);
}
.power-switch input:checked + .button .power-on .line {
	opacity: 1;
	transition: opacity 0.05s ease-in 0.55s;
}
.power-switch input:checked + .button .power-on .circle {
	transform: rotate(302deg);
	stroke-dashoffset: 40;
	transition: transform 0.4s ease 0.2s, stroke-dashoffset 0.4s ease 0.2s;
}
@keyframes line-animation {
	0% {
		transform: translateY(0);
	}
	10% {
		transform: translateY(10px);
	}
	40% {
		transform: translateY(-25px);
	}
	60% {
		transform: translateY(-25px);
	}
	85% {
		transform: translateY(10px);
	}
	100% {
		transform: translateY(0px);
	}
}
@keyframes click-animation {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(0.9);
	}
	100% {
		transform: scale(1);
	}
}
/* burger */
.burger {
	width: var(--btnSize);
	height: var(--btnSize);
	border-radius: var(--radius1);
}
.burger:hover {
	cursor: pointer;
}
.burger__container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	padding: 1rem 0.7rem;
}

.lines {
	width: 100%;
	height: 0.2rem;
	background-color: var(--text2);
}
.line2 {
	width: 100%;
	align-self: flex-end;
}
.line3 {
	width: 75%;
	align-self: flex-end;
}
.activeMenu .lines {
	background-color: var(--akcent);
	width: 80%;
}
.active .line2 {
	width: 75%;
}
.active .line3 {
	width: 100%;
}
</style>