export default {
	methods: {
		dateClass(ymd) {
			const tasks = this.tasks;
			const holidays = this.holidays
			let isContainTask=false;
			let isContainHol = false;
			try{
				for(let task of tasks) {
					if(task.date===ymd) {
						isContainTask=true
						break
					}
				}
				for(let hol of holidays) {
					let [,mm,dd] = hol.date.split('-')
					const checkDate = `2021-${mm}-${dd}`
					if(checkDate===ymd) {
						isContainHol=true
						break
					}
				}
			}catch(error){
				console.error(error);
			}	
			if(isContainTask&&isContainHol) return 'taskHolMark'
			if(isContainTask) return 'taskMark'
			if(isContainHol) return 'holMark'
			else return 'empty'
		}
	},
	computed: {
		dateFilteredTasks(){
			try{
				const tasks = this.tasks
				let selDate = new Date().toISOString();
				if(this.calendarCtx) selDate = this.calendarCtx.selectedYMD
				let result=[]
				tasks.forEach(task=>{
				if(task.date===selDate) {
					result.push(task)
				}
			})
			return result
			}catch(error){
				console.error(error);
			}
		},
		dateFilteredHolidays(){
			try{
				const holidays = this.holidays
				let selectedDate = new Date().toISOString();
				if(this.calendarCtx) selectedDate = this.calendarCtx.selectedYMD
				const selDate = `${selectedDate.split('-')[1]}-${selectedDate.split('-')[2]}`
				let result = [];
				holidays.forEach(holiday=>{
					const holidayDate = holiday.date.split('-')
					const holDate = `${holidayDate[1]}-${holidayDate[2]}`
					if(selDate===holDate) result.push(holiday)
			})
			return result
			}catch(error){
				console.error(error);
			}
		},
	}
}