<template lang="pug">
.item.location.neuro
	.item__title
		h2 Location
	.item__content.location__content(v-if="loc.country_name")
		.top
			.country
				h3 {{ loc.country_name }}
				img(
						:src="`https://www.countryflags.io/` + loc.country_code + `/shiny/32.png`"
					)
			h3 {{ loc.city }}
		.bottom
			.geo 
				b-icon(icon="geo-alt")
				.latLon 
					.lat {{ loc.latitude }}
					.lon {{ loc.longitude }}
			.zip 
				b-icon(icon="signpost-split")
				.zipCode {{ loc.postal }}
	.error-content(v-else) 
		h3 failed to load data

</template>

<script>
import { mapGetters } from "vuex";
export default {
	data: () => ({}),
	computed: {
		...mapGetters(["loc"])
	}
};
</script>

<style lang="sass" scoped>
.location
	&__content
		flex-direction: column
		justify-content: center
		.top
			display: flex
			justify-content: center
			flex-direction: column
			h3
				text-align: center
			.country
				display: flex
				width: auto
				img
					margin-left: .2rem
		.bottom
			margin-top: .5rem
			.lon
				margin-top: .2rem
			.zip
				align-items: baseline

.country,.geo,.zip
	width: 100%
	display: flex
	align-items: center
	justify-content: center
	.b-icon.bi
		margin-left: 0rem
		margin-right: .5rem
		font-size: 1.2rem

</style>