const weather = {
	state: ()=>({
		weather:{}
	}),
	mutations: {
		LOAD_WEATHER(state, weather){
			state.weather=weather
		},
	},
	actions: {
		async fetchWeather({commit, rootState}){
			let serverResp=true;
			const url = "https://api.openweathermap.org/data/2.5/";
			const key = "250e5867d9496bc724aeb53dffc2e220";
			try {
				const lat = rootState.loc.loc.latitude;
				const lon = rootState.loc.loc.longitude;
				const weather = await (await fetch(`${url}onecall?lat=${lat}&lon=${lon}&exclude=hourly&appid=${key}`)).json();
				commit('LOAD_WEATHER', weather);
			} 
			catch(error){
				console.error(error);
				serverResp=false;
			}
			commit('SET_SERVER_RESPOND',{weather:serverResp})
		},
	},
	getters: {
		weather: state=>state.weather,
	},
}

export default weather