<template lang="pug">
.page.task-page.neuro
	.page__container.task-page__container 
		.page__title 
			h2 new task
		.task-page__content 
			h3 Title
			b-row.mb-5
				.neuro-input
					input(
						v-model="task.title",
						type="text",
						placeholder="Example",
						ref="inputTitle"
					)
			h3 Decription
			b-row.mb-3
				.neuro-input
					textarea(v-model="task.description", type="text", placeholder="")
			h3 expire time
			b-row.timestamp(align-h="between")
				b-col.box-item.neuro-input(cols="12", sm="6")
					b-form-datepicker.task-page__date-picker(v-model="task.date", locale="en")
				b-col.box-item.neuro-input.mt-3.mt-sm-0(cols="12", sm="3")
					b-form-timepicker.neuro-input.task-page__time-picker(
						v-model="task.time",
						locale="en"
					)
			b-row.btns-row.mt-4
				button.neuro(@click="addTask", :disabled="task.title.length < 1") add
					b-icon(icon="plus-circle", font-scale="1.5")
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
	data: () => ({
		task: {
			title: "New Note",
			description: "",
			date: "",
			time: "00:00:00",
			status: "active"
		}
	}),
	methods: {
		...mapMutations(["ADD_TASK", "STATUS_UPDATE"]),
		addTask() {
			const task = { ...this.task, id: this.idGenerator() };
			this.ADD_TASK(task);
			this.STATUS_UPDATE();
			this.$router.go(-1);
		},

		idGenerator() {
			const alphabet = new Array(26)
				.fill(1)
				.map((_, index) => String.fromCharCode(65 + index));

			const random = max => Math.floor(Math.random() * Math.floor(max));

			const pair = () =>
				`${random(10)}${alphabet[random(alphabet.length - 1)]}`;

			return `#${pair()}${pair()}${pair()}`;
		}
	},
	computed: {
		...mapGetters(["tasks", "calendarCtx"])
	},
	mounted() {
		this.task.date = this.calendarCtx.selectedYMD;
		this.$refs.inputTitle.focus();
	}
};
</script>

<style lang="sass" scoped>
</style>