<template lang="pug">
#app
	transition(name="fade")
		MainLoader(v-show="!dataFullLoaded")
	RotateScreen
	TopBar
	.content-part
		transition(name="fade")
			router-view
				Home
	transition(name="fade")
		Menu(v-show="isActiveMenu") 
</template>

<script>
import { mapActions, mapMutations, mapGetters } from "vuex";

import TopBar from "./components/TopBar";
import Home from "./views/Home";
import RotateScreen from "./views/RotateScreen";
import Menu from "./components/Menu";
import MainLoader from "./views/MainLoader";

export default {
	name: "App",
	components: {
		TopBar,
		Home,
		RotateScreen,
		Menu,
		MainLoader
	},
	data: () => ({
		isLandsacape: false
	}),
	methods: {
		...mapActions([
			"fetchIp",
			"fetchLoc",
			"fetchWeather",
			"fetchCovid",
			"initCheckFullLoaded",
			"fetchHolidays",
			"fetchNews"
		]),
		...mapMutations(["LOAD_TASKS_FROM_LOCAL", "STATUS_UPDATE"]),
		async fetchData() {
			this.LOAD_TASKS_FROM_LOCAL();
			this.STATUS_UPDATE();
			await this.fetchIp();
			await this.fetchLoc();
			await this.fetchWeather();
			await this.fetchCovid();
			await this.fetchHolidays();
			await this.fetchNews();
			this.initCheckFullLoaded();
		}
	},
	computed: {
		...mapGetters(["dataFullLoaded", "isActiveMenu", "tasks"])
	},
	created() {
		this.fetchData();
	},
	mounted() {
		this.$router.push({ name: "home" })
	}
};
</script>

