<template lang="pug">
.page.tasks-page.neuro
	.page__container.tasks-page__container 
		.page__title 
			h2 Tasks
		.tasks-page__content 
			.filter
				select.pointer(v-model="filter") 
					option(value="a-z") A-Z
					option(value="date", selected) Date
					option(value="status") Status
				.reverseArray.pointer(@click="filterDirUp = !filterDirUp")
					b-icon(icon="arrow-down-up")
			.tasks-page__inner.thin-scroll-bar
				.neuro-input(v-for="task in filteredTasks")
					router-link.task(
						:to="{ name: 'EditTask', params: { id: task.id, task: task } }"
					)
						.task__id 
							span {{ task.id }}
						.task__title
							h2 {{ task.title }}
						.task__time 
							p {{ task.date }}
							p {{ `${task.time.split(':')[0]}:${task.time.split(':')[1]}` }}
						.task__status
							.dot(:class="task.status") 
							span {{ task.status }}
		.neuro.add-note
			router-link(:to="{ name: 'NewTask' }") 
			h2 add task

</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import commonTasksMethods from "../mixins/commonTasksMethods";
export default {
	data: () => ({
		filter: "date",
		filterDirUp: true
	}),
	mixins: [commonTasksMethods],
	methods: {
		...mapMutations(["STATUS_UPDATE"])
	},
	computed: {
		...mapGetters(["tasks"]),
		filteredTasks() {
			const tasks = this.tasks;
			switch (this.filter) {
				case "status":
					tasks.sort((a, b) => {
						if (a.status < b.status) return -1;
					});
					break;
				case "a-z":
					tasks.sort((a, b) => {
						if (a.title.toLowerCase() < b.title.toLowerCase())
							return -1;
					});
					break;
				case "date":
					tasks.sort((a, b) => {
						const checkDateA = new Date(
							`${a.date}T${a.time}`
						).toISOString();
						const checkDateB = new Date(
							`${b.date}T${b.time}`
						).toISOString();
						if (checkDateA < checkDateB) return -1;
					});
					break;
			}
			if (this.filterDirUp) return tasks;
			else return tasks.reverse();
		}
	},
	watch: {},
	mounted() {
		this.STATUS_UPDATE();
	}
};
</script>
<style lang="sass" scoped>
.tasks-page
	.add-note
		margin-top: 1rem
		padding: 1.5rem 0rem
	&__content
		margin-top: 2rem
		.neuro-input
			margin-bottom: 2rem
			width: 98%
	.filter
		width: 100%
		margin-bottom: 2rem
		display: flex
		justify-content: flex-start
		.reverseArray
			margin-left: 1rem
			display: flex
			align-items: center
			.b-icon
				font-weight: 800
				font-size: 1.5rem
		select
			padding: .3rem
			background-color: var(--bg2)
			color: var(--text2)
			border: 1px solid var(--text2)
			+MW768
				color: black
				background-color: auto
			option
				width: 100%
				font-size: 1.5rem
	&__inner
		max-height: 350px
		overflow-y: auto
	.task
		display: grid
		grid-template-columns: 1fr 2fr 2fr 1fr
		align-items: center
		padding: 1rem 1.5rem
		&__id
			span
				color: var(--text2)
		&__status
			display: grid
			grid-template-columns: 1fr 1fr
			justify-items: center
			align-items: center
			.dot
				width: 8px
				height: 8px
				border-radius: 50%
				margin-right: .3rem
			span
				font-weight: 800
				font-size: .8rem
		&__time
			display: flex
			align-items: center
			flex-direction: column
</style>