<template lang="pug">
.page.news-page.neuro
	.page__container.news-page__container(v-if="news.articles") 
		.page__title 
			h2 news
		.news-page__items.thin-scroll-bar
			.news-page__item.neuro-input(v-for="(n, i) in news.articles", :key="n.id") 
				.title.pointer(@click="activeAccordion(i)") 
					span {{ n.title }}
				a.accordion.d-none(:href="`${n.url}`", :ref="`accordion-body` + i") 
					.image-side(v-if="n.urlToImage != undefined")
						img(:src="`${n.urlToImage}`")
					.right-side
						span {{ n.publishedAt.split('T')[0] }}
						p {{ n.description }}
	.error-content(v-else) 
		h3 failed to load data
</template>

<script>
import { mapGetters } from "vuex";
export default {
	data: () => ({
	}),
	methods: {
		activeAccordion(i) {
			const el = this.$refs["accordion-body" + i][0];
			if (!el.classList.contains("d-none")) el.classList.add("d-none");
			else el.classList.remove("d-none");
		}
	},
	computed: {
		...mapGetters(["news"])
	}
};
</script>

<style lang="sass" scoped>
.news-page
	&__items
		max-height: 400px
		overflow-y: auto
		overflow-x: hidden
		margin-top: 2rem
	&__item
		padding: 1rem 1rem 1.7rem 1rem
		margin: 0rem 0rem 1rem 0rem
		width: 97%
		span
			font-size: 1.1rem
			font-family: "Raleway"
		.accordion
			display: flex
			margin-top: 1rem
			.image-side
				margin-right: 1rem
				img
					width: 100px
					height: 100%
					object-fit: cover
			.right-side
				display: flex
				flex-direction: column
				justify-content: space-between
				span
					color: var(--text2)
					margin-bottom: 1rem
</style>