const covid = {
	state: ()=>({
		covid:{}
	}),
	mutations: {
		LOAD_COVID(state,covid){
			state.covid=covid;
		},
	},
	actions: {
		async fetchCovid({commit, rootState}){
			let serverResp=true;
			const [yyyy,mm,dd] = [new Date().getFullYear(),new Date().getMonth()+1,new Date().getDate()];
			try {
				const country = rootState.loc.loc.country_name.toLowerCase();
				const region = rootState.loc.loc.region;
				const url = `https://api.covid19tracking.narrativa.com/api/${yyyy}-${mm}-${dd}/country/${country}/region/${region}`;
				const covid = await (await fetch(`${url}`)).json();
				const keysDates =Object.keys(covid.dates);
				const keysCountry =Object.keys(covid.dates[keysDates[0]].countries);
				const res = covid.dates[keysDates[0]].countries[keysCountry[0]];
				commit("LOAD_COVID", res);
			}
			catch(error){
				console.error(error);
				serverResp=false;
			}
			commit('SET_SERVER_RESPOND',{covid:serverResp})
		},
	},
	getters: {
		covid: state=>state.covid,
	},
}
export default covid