import Vue from 'vue'
import Vuex from 'vuex'

import tasks from './modules/tasks'
import ip from './modules/ip'
import loc from './modules/loc'
import weather from './modules/weather'
import covid from './modules/covid'
import holidays from './modules/holidays'
import news from './modules/news'

Vue.use(Vuex)

export default new Vuex.Store({
	modules: {
		tasks,
		ip,
		loc,
		weather,
		covid,
		holidays,
		news
	},
	actions: {
		initCheckFullLoaded({commit}){
			commit("CHECK_FULL_LOADED")
		}
	},
	mutations: {
		SET_CALENDAR_CTX(state, ctx){
			state.calendarCtx=ctx;
		},	
		MENU_CHANGER(state){
			state.isActiveMenu=!state.isActiveMenu;
		},
		SET_SERVER_RESPOND(state, obj){
			state.serversResponds.push(obj)
		},
		CHECK_FULL_LOADED(state){
			setTimeout(() => {
				state.isDataFullLoaded=true;
			}, 1000);
		},
	},
	state: {
		isDataFullLoaded: false,
		calendarCtx: {
			selectedYMD: new Date().toISOString().split('T')[0]
		},
		isActiveMenu: false,
		serversResponds:[]
	},
	getters: {
		calendarCtx: state => state.calendarCtx,
		dataFullLoaded: state=>state.isDataFullLoaded,
		isActiveMenu:state=>state.isActiveMenu
	}
})