const loc = {
	state: ()=>({
		loc:{}
	}),
	mutations: {
		LOAD_LOC(state,loc){
			state.loc=loc;
		},
	},
	actions: {
		async fetchLoc({commit, rootState}){
			let serverResp=true;
			try{
				const ip = rootState.ip.ip;
				const loc = await (await fetch(`https://ipapi.co/${ip}/json/`)).json()
				localStorage.setItem('loc',JSON.stringify(loc))
				commit('LOAD_LOC', loc);
			}catch(error){
				console.error(error);
				const localLoc = JSON.parse(localStorage.getItem('loc'))
				if(localLoc) commit('LOAD_LOC', localLoc);
				else serverResp=false;
			}
			commit('SET_SERVER_RESPOND',{loc:serverResp})
		},
	},
	getters: {
		loc: state=>state.loc
	},
}
export default loc