<template lang="pug">
.page.covid-page.neuro
	.page__container.covid-page__container 
		.page__title 
			h2 Covid Data
		.covid-page__subtitle 
			h3 {{ covid.name }}
		.covid-page__content 
			.covid-item.neuro-input
				span Source: 
				span {{ covid.source }}
			.covid-item.neuro-input 
				span Total confirmed: 
				span {{ covid.today_confirmed }}
			.covid-item.neuro-input 
				span Total deaths: 
				span {{ covid.today_deaths }}
			.covid-item.neuro-input 
				span Today confirmed: 
				span {{ covid.today_new_confirmed }}
			.covid-item.neuro-input 
				span Today deaths: 
				span {{ covid.today_new_deaths }}
			.covid-item.neuro-input 
				span Yesterday confirmed: 
				span {{ covid.yesterday_confirmed }}
			.covid-item.neuro-input 
				span Yesterday deaths: 
				span {{ covid.yesterday_deaths }}
</template>

<script>
import { mapGetters } from "vuex";

export default {
	data: () => ({}),
	computed: {
		...mapGetters(["covid"])
	}
};
</script>

<style lang="sass" scoped>
.covid-page
	&__container
	&__subtitle
		font-weight: 800 
		font-size: 1.5rem
		font-family: "Raleway"
		margin-top: 1.3rem
	&__content
		.covid-item
			margin: 1rem 0rem
			padding: 1rem 1rem 1.8rem 1rem
			span 
				font-weight: 800
				font-size: 1rem
</style>