<template lang="pug">
.item.weather.neuro 
	.item__title
		h2 weather
	.item__content.weather__content(v-if="weather.current") 
		.top
			.description
				.temp {{ Math.round(weather.current.temp - 273) }}&degС
				h3 {{ weather.current.weather[0].description }}
			.image
				img(
					:src="`http://openweathermap.org/img/wn/` + weather.current.weather[0].icon + `.png`"
				) 
		.bottom
			.detail-line
				.humidity 
					fa-icon(:icon="['fa', 'tint']")
					span {{ weather.current.humidity }}%
				.wind 
					fa-icon(:icon="['fa', 'wind']")
					span {{ weather.current.wind_speed }}m/s
			.temp-line 
				.min 
					span min
					span {{ Math.round(weather.daily[0].temp.min - 273) }}&degС
				.max 
					span max
					span {{ Math.round(weather.daily[0].temp.max - 273) }}&degС
	.error-content(v-else) 
		h3 failed to load data
</template>

<script>
import { mapGetters } from "vuex";
export default {
	data: () => ({}),
	computed: {
		...mapGetters(["weather"])
	}
};
</script>

<style lang="sass" scoped>
.weather
  &__content
    flex-direction: column
    align-items: center
    justify-content: center
  .top
    display: flex
    align-items: center
    border-bottom: .1rem solid var(--transp1)
    padding-bottom: .5rem
  .image
    background-color: var(--grey)
    margin-left: 1rem
    border-radius: var(--radius1)
    img
      width: 40px
      height: 40px
  .temp
    white-space: nowrap
    font-size: 2.4rem
    +MW350
      font-size: 1.5rem
  h3
    font-size: 1rem
    margin-top: .5rem
  .bottom
    display: flex
    flex-direction: column
    margin-top: .5rem
    .detail-line
      display: flex
      justify-content: space-evenly
      .wind
        margin-left: .5rem
      span
        margin-left: .3rem
    .temp-line
      display: flex
      margin-top: .5rem
      span
        &:last-child
          margin-left: .3rem
      .max
        margin-left: .5rem
</style>