<template lang="pug">
.page.weather-page.neuro
	.page__container
		.page__title 
			h2 Weather Forecast
		.weather-page__content 
			.current
				.left
					.image
						img(
							:src="`http://openweathermap.org/img/wn/` + weather.current.weather[0].icon + `.png`"
						) 
					.description
						.temp {{ Math.round(weather.current.temp - 273) }}&degС
						h3 {{ weather.current.weather[0].description }}
				.right
					.detail-line
						.humidity 
							fa-icon(:icon="['fa', 'tint']") 
							span {{ weather.current.humidity }}%
						.min 
							span min:
							span {{ Math.round(weather.daily[0].temp.min - 273) }}&degС
						.wind 
							fa-icon(:icon="['fa', 'wind']")
							span {{ weather.current.wind_speed }}m/s
						.max 
							span max:
							span {{ Math.round(weather.daily[0].temp.max - 273) }}&degС
			.daily
				.daily__items(v-for="(cast, i) in forecast") 
					.daily__item.neuro-input(v-show="i!=0&&i<6")
						.day 
							span {{ dateFormat(cast.dt) }}
						.day-temp 
							span {{ Math.round(cast.temp.day - 273) }}&degС
						.day-image 
							img(
								:src="`http://openweathermap.org/img/wn/` + cast.weather[0].icon + `.png`"
							)
						.day-humidity 
							fa-icon(:icon="['fa', 'tint']") 
							span {{ cast.humidity }} %
						.day-wind
							fa-icon(:icon="['fa', 'wind']")
							span {{ Math.round(cast.wind_speed) }}m/s
</template>

<script>
import { mapGetters } from "vuex";
export default {
	data: () => ({
		forecast: []
	}),
	computed: {
		...mapGetters(["weather"])
	},
	methods: {
		loadForecast() {
			this.forecast = this.weather.daily;
		},
		dateFormat(timestamp) {
			const newDate = new Date(timestamp * 1000);
			const months = [
				"Jan",
				"Feb",
				"Mar",
				"Apr",
				"May",
				"Jun",
				"Jul",
				"Aug",
				"Sep",
				"Oct",
				"Nov",
				"Dec"
			];
			const month = months[newDate.getMonth()];
			const date = newDate.getDate();
			const time = `${date} ${month}`;
			return time;
		}
	},
	mounted() {
		this.loadForecast();
	}
};
</script>

<style lang="sass" scoped>
.weather-page
	&__content
		margin-top: 2rem
	.current
		display: flex
		.left
			display: flex
			.image
				padding: .3rem
				display: flex
				justify-content: center
				align-items: center
				background-color: var(--grey)
				border-radius: var(--radius1)
			.description
				display: flex
				align-items: center
				justify-content: center
				flex-direction: column
				margin-left: 2rem
				.temp
					font-size: 2.5rem
				h3
					font-size: 1.5rem
					font-family: 'Raleway'
					font-weight: 800
		.right
			display: flex
			margin-left: 2rem
			align-items: center
			font-weight: 800
			.detail-line
				height: 100%
				display: grid
				grid-template-columns: 1fr 1fr
				svg
					width: 15px
					height: 15px
					margin-right: .5rem
				.humidity,.wind,.min,.max
					display: flex
					align-items: center
			+MW350
				display: none
.daily
	margin-top: 2rem
	&__items 
		width: 97%
	&__item
		display: flex
		justify-content: space-between
		padding: 1rem 2rem
		align-items: center
		margin: 1.5rem 0rem
		.day
			span
				font-weight: 800
		.day-image
			background-color: var(--grey)
			border-radius: var(--radius1)
			img
				width: 20px
				height: 20px
		.day-humidity,.day-wind
			span
				margin-left: .4rem
</style>