<template lang="pug">
.item.calendar.neuro 
	.desktop
		b-calendar(
			v-model="value",
			@context="onContext",
			locale="en-US",
			:date-info-fn="dateClass",
			hide-header
		)
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import commonTasksMethods from "../../mixins/commonTasksMethods";
export default {
	mixins: [commonTasksMethods],
	data: () => ({
		value: new Date(),
		context: null
	}),
	methods: {
		...mapMutations(["SET_CALENDAR_CTX"]),
		onContext(ctx) {
			this.SET_CALENDAR_CTX(ctx);
		}
	},
	computed: {
		...mapGetters(["tasks", "calendarCtx", "holidays"])
	}
};
</script>

