<template lang="pug">
.item.covid.neuro 
	.item__title
		h2 covid-19
	.item__content.covid__content(v-if="covid.today_confirmed")
		.image
			img(src="../../assets/img/cor.png")
		table 
			thead 
				th
				th Total
				th Today
			tbody 
				tr 
					td.td-title confirmed:
					td.td-num {{ covid.today_confirmed }}
					td.td-num {{ covid.today_new_confirmed }}
				tr 
					td.td-title deaths:
					td.td-num {{ covid.today_deaths }}
					td {{ covid.today_new_deaths }}
				tr 
					td.td-title recovered:
					td.td-num {{ covid.today_recovered }}
					td.td-num {{ covid.today_new_recovered }}
	.error-content(v-else) 
		h3 failed to load data
</template>

<script>
import { mapGetters } from "vuex";
export default {
	data: () => ({}),
	computed: {
		...mapGetters(["covid"])
	}
};
</script>

<style lang="sass" scoped>
.covid
	&__content
		display: flex
		flex-direction: row
		justify-content: space-between
		+MW370 
			justify-content: center
	.image
		display: flex
		align-items: flex-end
		height: 100%
		+MW370
			display: none
	img
		max-width: 80px
		max-height: 80px
		object-fit: cover
		+MW400
			max-width: 60px
			max-height: 60px
	table
		width: 60%
		height: 100%
	th
		border-bottom: solid 1px var(--transp1)
		padding-bottom: .3rem
		font-weight: 700
		text-align: center
	td
		text-align: center
		&:nth-of-type(2)
			padding: .5rem .5rem
			+MW350 
				padding: .2rem .5rem
	.td-title
		font-weight: 800
		font-size: .9rem
	.td-num
		font-style: italic
	&__left-side
		display: flex
		align-items: center
</style>