<template lang="pug">
.item.tasks.neuro.pointer
	.desktop
		.item__title
			h2 tasks
		.item__content.tasks__content 
			.setted-date 
				h3 {{ calendarCtx.selectedFormatted }}
			.tasks__today.thin-scroll-bar(v-if="dateFilteredTasks[0]")
				.task.neuro-input(v-for="(task, i) in dateFilteredTasks", :key="task.id")
					.task-id
						span {{ i + 1 }}
					.task-title(:class="{ lineThrough: task.status == 'done' }") 
						span {{ task.title }}
					.task-status 
						.dot(:class="task.status")
					.task-controls
						button(@click.stop="toEditTask(task)") edit
						b-icon.pointer(
							@click.stop="STATUS_CHANGER(task.id)",
							icon="check2-all",
							:class="{ checkedColor: task.status == 'done' }"
						)
						b-icon.pointer(@click.stop="DELETE_TASK(task.id)", icon="trash")
			.tasks__today.thin-scroll-bar(v-else)
				h3 No tasks for this day
			.divide-h-line 
			.tasks__holidays.thin-scroll-bar(v-if="dateFilteredHolidays.length > 0")
				ul(v-for="holiday in dateFilteredHolidays", :key="holiday.id")
					li {{ holiday.name }}
			.tasks__holidays(v-else) 
				h3 No holidays
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
import commonTasksMethods from "../../mixins/commonTasksMethods";
export default {
	mixins: [commonTasksMethods],
	data: () => ({}),
	methods: {
		...mapMutations(["DELETE_TASK", "STATUS_CHANGER"]),
		toEditTask(t) {
			this.$router.push({
				name: "EditTask",
				params: { id: t.id, task: t }
			});
		}
	},
	computed: {
		...mapGetters(["tasks", "holidays", "calendarCtx"])
	},
};
</script>

<style lang="sass" >
.item,.tasks
	max-height: 27rem
.tasks
	&__content
		display: flex
		flex-direction: column
		h3
			text-align: end
	&__today
		width: 100%
		max-height: 14rem
		overflow-y: auto
		margin-top: 1rem
		background: var(--bg2)
		.task
			display: flex
			width: 97%
			justify-content: space-between
			align-items: center
			border-radius: var(--radius2)
			padding: .8rem 1rem
			margin: .5rem 0rem
			&:nth-last-child
				margin-bottom: 0rem
			&-id
				font-weight: 800
				color: var(--text2)
			&-title
				width: 50%
				font-family: "Raleway"
				white-space: nowrap
				overflow: hidden
				text-overflow: ellipsis
				&.lineThrough
					span
						opacity: .5
						text-decoration: line-through
			&-status
				.dot
					width: 10px
					height: 10px
					border-radius: 50%
					border: var(--text2) 1.5px solid
			&-controls
				button
					padding: .5rem
					background-color: var(--grey)
					border-radius: var(--radius1)
					text-transform: uppercase
					font-weight: 800
					font-size: .7rem
				.b-icon
					margin-left: .5rem
	&__holidays
		height: 3rem
		padding: 0rem 1rem
		overflow-y: auto
		ul
			li
				list-style: inside
.divide-h-line
	margin: 1rem 0rem
</style>