<template lang="pug">
.page.task-page.edit-task.neuro
	.page__container.task-page__container 
		.page__title 
			h2 edit note
			span {{ task.id }}
			p {{ task.status }}
		.task-page__content 
			h3 Title
			b-row.mb-5
				.neuro-input
					input(v-model="task.title", type="text", placeholder="Have fun")
			h3 Decription
			b-row.mb-3
				.neuro-input
					textarea(v-model="task.description", type="text", placeholder="")
			h3 expire time
			b-row.timestamp(align-h="between")
				b-col.box-item.neuro-input(cols="12", sm="6")
					b-form-datepicker.task-page__date-picker(v-model="task.date", locale="en")
				b-col.box-item.neuro-input.mt-3.mt-sm-0(cols="12", sm="3")
					b-form-timepicker.neuro-input.task-page__time-picker(
						v-model="task.time",
						locale="en"
					)
			b-row.btns-row.mt-4(align-h="between")
				b-col.btn.update-btn(cols="4")
					button.neuro(@click="updateTask", :disabled="task.title.length < 1") update
						b-icon(icon="arrow-repeat", font-scale="1.5")
				b-col.btn(cols="4")
					b-row(align-h="between")
						b-col(cols="5")
							button.neuro.back-btn(
								v-if="task.status == 'done'",
								@click="STATUS_CHANGER(task.id)"
							)
								fa-icon(:icon="['fa', 'times']")
							button.neuro.done-btn(v-else, @click="STATUS_CHANGER(task.id)") 
								b-icon(icon="check-circle-fill")
						b-col(cols="5")
							button.neuro.delete-btn(@click="delTask") 
								b-icon(icon="trash-fill")
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
import commonTasksMethods from "../mixins/commonTasksMethods";

export default {
	mixins: [commonTasksMethods],
	data: () => ({
		task: {
			id: "#0A0A",
			title: "",
			description: "",
			date: "1970-01-01",
			time: "00:00:00",
			status: "active"
		}
	}),
	methods: {
		...mapMutations(["ADD_TASK", "DELETE_TASK", "STATUS_CHANGER"]),
		updateTask() {
			const task = { ...this.task };
			this.DELETE_TASK(task.id);
			this.ADD_TASK(task);
			this.$router.go(-1);
		},
		delTask() {
			this.DELETE_TASK(this.task.id);
			this.$router.go(-1);
		}
	},
	computed: {
		...mapGetters(["tasks", "calendarCtx"])
	},
	mounted() {
		this.task = this.$route.params.task;
	}
};
</script>

<style lang="sass" scoped>
.edit-task
	.btns-row
		.update-btn
			.b-icon.bi
				margin-left: .3rem
		.back-btn
			color: var(--akcent)
			font-size: 1.5rem
		.done-btn
			.b-icon.bi
				color: var(--green1)
		.delete-btn
			.b-icon.bi
				color: var(--red1)
		button
			.b-icon.bi
				margin-left: 0rem
.page__title
	display: flex
	justify-content: space-between
	span
		font-size: 2rem
		color: var(--text2)
		text-align: right
</style>