const news = {
	state: ()=>({
		news:{}
	}),
	mutations: {
		LOAD_NEWS(state,news){
			state.news=news;
		},
	},
	actions: {
		async fetchNews({commit, rootState}) {
			let serverResp=true;
			const apiKey= "20e768eed75a47dfac82f6edc72c3786"
			try {
				const query = `https://newsapi.org/v2/top-headlines?country=${rootState.loc.loc.country}&apiKey=${apiKey}`;
				const response = await fetch(query);
				const result = await response.json();
				commit('LOAD_NEWS', result);
			} catch (error) {
				console.error(error);
				serverResp=false;
			}
			commit('SET_SERVER_RESPOND',{news:serverResp})
		},
	},
	getters: {
		news: state=>state.news,
	},
}
export default news