import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

import NewTask from "../views/NewTask.vue"
import EditTask from "../views/EditTask.vue"
import Home from "../views/Home.vue"
import Location from "../views/Location.vue"
import Weather from "../views/Weather.vue"
import Covid from "../views/Covid.vue"
import News from "../views/News.vue"
import Calendar from "../views/Calendar.vue"
import Tasks from "../views/Tasks.vue"

export default new Router({
	mode: 'history',
	routes: [
		{
			path: '/home',
			name: 'home',
			component: Home
		},
		{
			path: '/newTask',
			name: 'NewTask',
			component: NewTask
		},
		{
			path: '/home/:id',
			name: 'EditTask',
			component: EditTask
		},
		{
			path: '/location',
			name: 'Location',
			component: Location,
		},
		{
			path: '/Weather',
			name: 'Weather',
			component: Weather,
		},
		{
			path: '/Covid',
			name: 'Covid',
			component: Covid
		},
		{
			path: '/News',
			name: 'News',
			component: News
		},
		{
			path: '/Calendar',
			name: 'Calendar',
			component: Calendar
		},
		{
			path: '/Tasks',
			name: 'Tasks',
			component: Tasks
		}
	]
})

